import React, { useState, useEffect } from 'react';
import {Button} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../../assets/css/Quiz.css';

export default function Buttons(props) {
    function clicksig() {
        props.siguientepregunta();
    }
    
    function clickant() {
        props.anteriorpregunta();
    }

          return(
              <div id="botones">
              <Button variant="light" id="boton1" onClick={clicksig} className={props.num === 9 ? "no-clickable" : "clickable"}>{props.siguiente}</Button>
              <Button variant="light" id="boton2" onClick={clickant} className={props.num === 0 ? "no-clickable" : "clickable"}>{props.anterior}</Button>
              </div>
          );
      }
  
