import React, { useState, useEffect } from 'react';
import Header from '../components/HomeComponents/Header.jsx';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/TicTacToe.css';
import ES from "../assets/json/es.json"
import EN from "../assets/json/en.json"


export default function Home(props) {

  let titulo1 = props.text.titulo1;
  let titulo2 = props.text.titulo2;
  let frontimage = props.text.frontimage;

  return (
    <div>
      <Header titulo1={titulo1} titulo2={titulo2} frontimage={frontimage}/>
    </div>
    
  );

}