import { quizzes } from "../assets/json/mock-data";
import React, { useState, useEffect } from 'react';
import Header from '../components/QuizComponents/Header.jsx';
import Pregunta from '../components/QuizComponents/Pregunta.jsx';
import Buttons from '../components/QuizComponents/Buttons.jsx';
import '../assets/css/Quiz.css';

export default function Quiz(props) {

    const [quiz, setQuiz] = useState(quizzes);
    const [num, setNum] = useState(0);
    const [numpreg, setNumpreg] = useState(num + 1);

    function siguientepregunta(){
      setNum(num + 1);
      setNumpreg(numpreg + 1);
    }

    function anteriorpregunta(){
      setNum(num - 1);
      setNumpreg(numpreg - 1);
    }

    let id = quiz[num]["id"];
    let pregunta = quiz[num]["question"];
    let respuesta = quiz[num]["answer"];
    let fotopregunta = quiz[num]["attachment"]["url"];
    let fotoautor = quiz[num]["author"]["photo"]["url"];
    let nombreautor = quiz[num]["author"]["profileName"];


    let titulo5 = props.text.titulo5;
    let titulo6 = props.text.titulo6;
    let respuestatext = props.text.respuestatext;
    let botoncorregir = props.text.botoncorregir;
    let textocorregir = props.text.textocorregir;
    let puntuacion = props.text.puntuacion;
    let respuestainput = props.text.respuestainput;
    let titulocorrecto = props.text.titulocorrecto;
    let textocorrecto = props.text.textocorrecto;
    let textoincorrecto = props.text.textoincorrecto;
    let botoncerrar = props.text.botoncerrar;
    let siguiente = props.text.siguiente;
    let anterior = props.text.anterior;

    return (
      <div>
        <Header titulo5={titulo5}/>
        <Pregunta botoncerrar={botoncerrar} textoincorrecto={textoincorrecto} textocorrecto={textocorrecto} titulocorrecto={titulocorrecto} respuestainput={respuestainput} puntuacion={puntuacion} textocorregir={textocorregir} botoncorregir={botoncorregir} respuestatext={respuestatext} titulo6={titulo6} pregunta={pregunta} respuesta={respuesta} fotopregunta={fotopregunta} fotoautor={fotoautor} nombreautor={nombreautor} numpreg={numpreg} id={id}/>
        <Buttons siguiente={siguiente} anterior={anterior} siguientepregunta={siguientepregunta} anteriorpregunta={anteriorpregunta} num={num}/>
      </div>
      
    );
  
  }