import React from 'react';
import {Button, Dropdown} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/Home.css';
import logoes from '../assets/images/spain.png';
import logoen from '../assets/images/eeuu.png';

export default function LangButton(props) {
  function clickes() {
    props.langClickes();
  }

  function clicken() {
    props.langClicken();
  }

  let idioma = props.text.idioma;

  return(
    <div id="boton">

        <Dropdown>
        
        <Dropdown.Toggle variant="light">
          {idioma}
        </Dropdown.Toggle>

        <Dropdown.Menu>
          <Dropdown.Item onClick={clickes}><img src={logoes} style={{width:40, marginRight: 10 }} />Español</Dropdown.Item>
          <Dropdown.Item onClick={clicken}><img src={logoen} style={{width:40, marginRight: 10  }} />Inglés</Dropdown.Item>
        </Dropdown.Menu>
        </Dropdown>
    </div>

  );
    
}