import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../../assets/css/TicTacToe.css';
import frontimagees from '../../assets/images/gamefrontes.jpg';
import frontimageen from '../../assets/images/gamefronten.jpg';



export default function Header(props) {
  let frontimage = frontimagees;
      
  if (props.frontimage === "frontimagees") {
    frontimage = frontimagees;
  } else {
    frontimage = frontimageen;
  }

    return (
      <div><div>
      <h1>{props.titulo1}</h1>
      <p>{props.titulo2}</p>
      </div>
      <div>
      <img id="frontpage" src={frontimage} alt="Frontimage"/>
      </div>
      </div>
    );
    }