import logo from './assets/images/emblem_games.png';
import './assets/css/App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Route, Routes, useNavigate} from 'react-router-dom';
import {Navbar, Nav} from 'react-bootstrap'
import React, { useState, useEffect } from 'react';
import Home from './components/Home';
import TicTacToe from './components/TicTacToe';
import Quiz from './components/Quiz';
import LangButton from './components/LangButton.jsx';
import ES from "./assets/json/es.json"
import EN from "./assets/json/en.json"

function App() {
  
  const [lang, setLang] = useState(ES);

  function langClickes(){
    setLang(ES);
  }

  function langClicken(){
    setLang(EN);
  }

let text = lang;

  return (
    <div className="root">
        <Navbar bg="dark" variant="dark">
          <Navbar.Brand>
          <a>
          <img src={logo} style={{width:50, marginLeft: 50}} />
          </a>
          </Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="/">{text.pagina1}</Nav.Link>
            <Nav.Link href="/tictactoe">{text.pagina2}</Nav.Link>
            <Nav.Link href="/quiz">{text.pagina3}</Nav.Link>
          </Nav>
          <LangButton langClickes={langClickes} langClicken={langClicken} text={text}></LangButton>
        </Navbar>

        

        <Routes>
          <Route path="/" element={<Home text={text}/>}/>
          <Route path="/tictactoe" element={<TicTacToe text={text}/>}/>
          <Route path="/quiz" element={<Quiz text={text}/>}/>
        </Routes>
    </div>
  );
}

export default App;
