import React, { useState, useEffect } from 'react';
import Header from '../components/TicTacToeComponents/Header.jsx';
import Board from '../components/TicTacToeComponents/Board.jsx';
import Reset from '../components/TicTacToeComponents/Reset.jsx';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/TicTacToe.css';



export default function TicTacToe(props) {

  let playerx = props.text.playerx;
  let player0 = props.text.player0;
  

  let PLAYERX = playerx;
  let PLAYER0 = player0;

  const [turn, setTurn] = useState(PLAYERX);
  const [moves, setMoves] = useState(0);
  const [values, setValues] = useState([
    ['-', '-', '-'],
    ['-', '-', '-'],
    ['-', '-', '-']
    ]);
  
  let titulo3 = props.text.titulo3;
  let titulo4 = props.text.titulo4;
  let textonummov = props.text.textonummov;
  let reiniciar = props.text.reiniciar;
  
  useEffect(() => {
    async function fetchData() {
      const res = await fetch("../assets/json/data.json");
      const myjson = await res.json();
      console.log(myjson);
      setTurn(myjson.turn);
      setMoves(myjson.moves);
      setValues(myjson.values);
    }

    fetchData();
  }, []);

  function appClick(rowNumber, columnNumber) {
      let valuesCopy = JSON.parse(JSON.stringify(values));
      let newMovement = turn === PLAYERX ? 'X' : '0';
      valuesCopy[rowNumber][columnNumber] = newMovement;
      setTurn(turn === PLAYERX ? PLAYER0 : PLAYERX);
      setValues(valuesCopy);
      setMoves(moves + 1); 
  }

  function resetClick(){
    setTurn(PLAYERX);
    setMoves(0);
    setValues([
      ['-', '-', '-'],
      ['-', '-', '-'],
      ['-', '-', '-']
    ]);
  }

  let text = turn;

  return (
    <div>
      <Header titulo3={titulo3} titulo4={titulo4} text={text}/>
      <h4><Board values={values}  appClick={appClick}/></h4>
      <h3 id="resetear">{textonummov} {moves}</h3>
      <p><Reset reiniciar={reiniciar} resetClick={resetClick}></Reset></p>
    </div>
    
  );


}