import React, { useState, useEffect } from 'react';
import {Button, Modal, Form} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../../assets/css/Quiz.css';
import fotopreguntaoff from '../../assets/images/fotooffline.jpg';
import fotoautoroff from '../../assets/images/useroffline.png';

export default function Pregunta(props) {

    const [loginState, setLoginState] = useState({
        pregunta1: ""
    });

    const [correcto, setCorrecto] = useState("");
    const [show, setShow] = useState(false);
    const mySet = new Set();
    const [setcorrectas, setSetcorrectas] = useState(mySet);
    const [score, setScore] = useState(0);

    let textocorrecto = props.textocorrecto
    let textoincorrecto = props.textoincorrecto

    function appClick() {
        if (loginState.pregunta1 == props.respuesta) {
            setShow(true)
            setCorrecto(textocorrecto)
            setSetcorrectas(setcorrectas.add(props.id))
            setScore(setcorrectas.size)
        } else {
            setCorrecto(textoincorrecto)
            setShow(true)
        }
    }
      

    const handleClose = () => setShow(false);

          return(
              <div id="pregunta">
              <h1 id="titulo">{props.titulo6} {props.numpreg}: {props.pregunta === null ? "Pregunta Inválida":props.pregunta}</h1>
              <img id="fotopregunta" src={props.fotopregunta === null ? fotopreguntaoff:props.fotopregunta}/>
              <h1 id="titulo">{props.respuestatext}</h1>
              <Form id="formulario">
              <Form.Group>
              <Form.Control placeholder={props.respuestainput} onChange={(event) => { 
                  const value = event.target.value; 
                  setLoginState({ ...loginState, pregunta1: value})}}/>
              <Form.Text className="text-muted">
                    {props.textocorregir}
              </Form.Text>
              </Form.Group>
              <p><Button variant="light" onClick={appClick}>
                {props.botoncorregir}
              </Button></p>
              <Modal show={show} onHide={handleClose}>
              <Modal.Header closeButton>
              <Modal.Title>{props.titulocorrecto}</Modal.Title>
              </Modal.Header>
              <Modal.Body>{correcto}</Modal.Body>
              <Modal.Footer>
              <Button variant="secondary" onClick={handleClose}>
                {props.botoncerrar}
              </Button>
              </Modal.Footer>
              </Modal>
              </Form>
              <h4>{props.nombreautor === null ? "Anonimo":props.nombreautor} <img id="fotoautor" src={props.fotoautor === null ? fotoautoroff:props.fotoautor}/></h4>
              <h4><div id="puntuacion">{props.puntuacion} {score} </div> </h4>
              </div>
          );
      }
  
